    max-width: 1280px;
  }
}

.contacts {
  background-color: #fffdee;
  padding-top: 36px;
  padding-bottom: 30px;
}

.logo-link {
  letter-spacing: .3em;
  color: #494848;
  margin-bottom: 12px;
  font-family: Film P3, sans-serif;
  font-size: 40px;
  line-height: 1.07;
  display: block;
}

.contacts__title {
  letter-spacing: .02em;
  color: #5f5f5f;
  margin-bottom: 16px;
  font-family: Montserrat-SemiBold, sans-serif;
  font-size: 16px;
  line-height: 1.25;
}

.contacts-item {
  align-items: center;
  gap: 22px;
  margin-bottom: 16px;
  display: flex;
}

.contacts__text {
  width: 102px;
  letter-spacing: .02em;
  color: #c79002;
  font-family: Montserrat-Medium, sans-serif;
  font-size: 20px;
  line-height: 1.2;
}

.phone-wrap {
  background-color: #fff;
  border-radius: 2px;
  outline: .4px solid #f3c653;
  gap: 12px;
  padding: 9px 8px 7px;
  display: flex;
}

.phone-text {
  letter-spacing: .02em;
  color: #494848;
  align-items: center;
  font-family: Montserrat-Regular, sans-serif;
  font-size: 14px;
  line-height: 1.21;
  display: flex;
}

.phone-text:first-child:before {
  content: "";
  width: 24px;
  height: 24px;
  background-image: url("fluent_phone-24.882b44ef.svg");
  background-position: center;
  margin-right: 8px;
}

.address {
  width: 260px;
  background-color: #fff;
  border-radius: 2px;
  outline: .4px solid #f3c653;
  margin-left: auto;
  padding: 8px 33px 7px 10px;
  display: flex;
}

.address-link {
  letter-spacing: .01em;
  color: #494848;
  align-items: center;
  font-family: Montserrat-Regular, sans-serif;
  font-size: 16px;
  line-height: 1.25;
  display: flex;
}

.address-link:before {
  content: "";
  width: 24px;
  height: 24px;
  background-image: url("location-pin.17fbbeca.svg");
  background-position: center;
  background-size: cover;
  margin-right: 10px;
  padding-left: 6px;
  padding-right: 6px;
  display: block;
}

.map {
  border: 1px solid #bababa;
  border-radius: 2px;
  box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, .25);
}

iframe {
  display: block;
  overflow: hidden;
}

